.slot {
  box-sizing: content-box;
  text-align: center;
  background-color: #eee;
  border: 6px solid #aaa;
  border-radius: 50%;
  place-items: center;
  font-family: sans-serif;
  font-size: x-large;
  display: grid;
  position: absolute;
  transform: translate(-50%, -50%);
}

.slot.active {
  background-color: #a1d7ea;
  border-color: #37afda;
}

.slot * {
  pointer-events: none;
  -webkit-user-select: none;
  user-select: none;
}
